@import url(https://fonts.googleapis.com/css2?family=Mali&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mali&family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arima+Madurai:wght@700&family=Mali&family=Martel+Sans:wght@300&family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
  background-color: #7D8597;
  background-image: url(/static/media/cardboard.490a0a58.png);
}

h1.title{
  color: #001845 ;
  font-family: 'Arima Madurai';
  font-size: 5vh;
  padding-left: 2vh;
  padding-bottom: 0;
  margin-bottom: 0;
  margin-top: 0;
}

p{
  margin-top: 0;
  padding-left: 4vh;
  color: #001845;
  font-size: 130%;
  font-family: 'Martel Sans', sans-serif;
}

section.text-block{
  display: flex;
  align-items: flex-start;
}

div.text-container{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 70%;
  margin-right: 5%;
}
.header-h1{
  font-family: 'Russo One', sans-serif;
  font-size: 300%;
  text-align: center;
  color: #001233;
  text-shadow: 2px 0 0 #0353A4, -2px 0 0 #0353A4, 0 2px 0 #0353A4, 0 -2px 0 #0353A4
  
}

.portifolio-header{
  display: flex;
  flex-direction: column;
  background-color: #001845;
  background-image: url(/static/media/Texture-Circuit-Board-Pcb-Background-Wallpaper-5962474.21435df7.png);
  margin-bottom: 2vh;

}

.header-nav{
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  text-align: center;
  flex-wrap: wrap;
}

.nav-button{
  background-color: #00123391;
  flex-grow: 1;
  border:dotted black 1px;
  padding: 1vh;
  text-decoration: none;
  font-family: 'Mali', cursive;
  color:#0353A4;
  font-size: 2.5vh;
}

.nav-button:hover{
  background-color: #0353A4;
  color:#00123391;
}


img.minha-foto{
  width: 30vh;
  margin: 0vh 0vh 5vh 5vh;
  object-fit: contain;
  border-radius: 10px;
  border: solid 1px #001845;;
}

img.keyboard{
  width: 15%;

  -webkit-animation: rotate 10s linear infinite;

          animation: rotate 10s linear infinite;
  align-self: center;
  justify-self: flex-start;
  

}

@-webkit-keyframes rotate{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes rotate{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}



.portfolio-block{
  padding-top: 1%;
}
.portfolio-button-icon{
  width: 5vh;
}

.portfolio-button{
  display: flex;
  align-items: center;
  color: #001845;
  font-family: 'Arima Madurai';
  font-size: 2vh;
  border: none;
  width: 100%;
  border: solid 1px #001233; 
  background-color: #979DAC;
  
}

.portfolio-button:hover{
  background-color: #33415C;
}

.portfolio-button.selected{
  background-color: #001233cc;
  color: #979DAC;
  border-style: solid;
  border-width: 1px;
  border-color:#001233 
}

.portfolio-button img{
  opacity: 0.5;
}

.portfolio-button.selected img{
  opacity: 0.8;
}


.portfolio-title{
  
  padding-right: 1%;
  color: #0353A4;
  font-family:'Russo One';
}

.title-container{
  
  justify-content: center;
  width: 70%;
  
}

.portfolio-title-img{
  height: 4vh;
}

.title-container{
  display: flex;
  align-items: center;
}


.projectlist{
  display: flex;
  background-color: #001845;
  width: 80%;
  margin: auto;
  margin-top: 2%;
  height: 50vh;
  align-items: center;
  justify-content: center;
  background-image: url(/static/media/why-us-background-texture@2x.9ef52dcc.png);
  background-repeat: repeat;
  background-size: contain;
  border-radius: 1vh 5vh 5vh 1vh;
}

.projectlist-buttons-container{
  display: flex;
  flex-direction: column;
}


.selected-project-show{
  background-color: #001233ab;
  width: 80%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1vh 5vh 5vh 1vh;
}

.project-text{
  background-color: #5C677D;
  color: black;
  width: 90%;
  height: 70%;
  margin: auto;
  margin-top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  padding-top: 3%;
  font-family: 'Martel Sans', sans-serif;
  color: #001233;
  overflow: auto;
}

.img-link{
  width: 50%;
  align-self: center;
}

.selected-project-show a{
  display: flex;
  flex-direction: column;
  width: 20%;
  align-items: center;
  align-self: center;
  font-family: 'Arima Madurai';
  text-decoration: none;
  color: #001233;
}
.footer-nav{
  display: flex;
  justify-content: space-around;
}

.footer-nav a{
  display: flex;
  align-items: center;
}

.footer-icon{
  width: 20vh;
}

.footer-icon:hover{
  opacity: 0.8;
}

.portfolio-footer{
  background-color: #0353A4;
  background-image: url(/static/media/45-degree-fabric-dark.3a1471f2.png);
  margin-top: 2%;
}
.ability-list{
  background-image: url(/static/media/cc4aa0877b182cff15c56b3963fbbd96.8dc5d11c.jpg);
  background-size: contain;
  border-radius: 0 5vh 5vh 0;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  margin: auto;
  padding: 1%;
}

.ability-buttons-container{
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
}

.show-selected-ability{
  width: 60%;
  background-color: #556380c7;
  margin-left: 1%;
  overflow-y: auto;
  overflow-x: hidden;
  height: 40vh;
  align-self: center;
}

.ability-title{
  font-size: 4vh;
  text-align: center;
  color: #001233
}

.title-image{
  width: 10% ;
  margin-left: 2%;
}

.title-container{
  display: flex;
  justify-content: center;
  width: 100%;
}
.ability-button{
  background-color: #33415C;
  display:flex;
  height: 7vh;
  width: 100%;
  align-items: stretch;
  justify-content: stretch;
  margin: 0;
  padding: 0;
  margin-bottom: 1%;
  border-radius: 1vh;
  border: none
}

.ability-button:hover {
  background-color: #5C677D;
}

.ability-button.selected{
  background-color: #5C677D;
}

.ability-button-icon{
  width: 20%;
  justify-content: center;
  align-items: center;
  margin: 1%;
  border-radius: 50%;
  
}

.ability-button-text{
  font-size: 140%;
  height: 40%;
  margin:0;
  padding: 0;
}

.ability-button-exp-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  justify-content: space-around;

}
.experience-bar{
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
}

.experience-container{
background-color: black;
width: 100%;
align-self: stretch;
display: flex;
justify-content: flex-start;
align-items: center;
padding: 1vh;
margin-right: 2%;
border-radius: 10vh;
}

.experience-background{
  background-color: #7D8597;
  width: 100%;
  height: 100%;
  border-radius: 10vh;
}

.experience{
  background-color: #023E7D;
  background-image: url(/static/media/gradient.fa66324b.gif);
  background-size: 100% 100%;
  width: 0%;
  height: 100%;
  transition: linear 2s;
  border-radius: 10vh;
}

.experience-text{
  font-size: 2vh;
  color: #001845;
}
.ability-block{
  margin-bottom: 1%;
}
