img.minha-foto{
  width: 30vh;
  margin: 0vh 0vh 5vh 5vh;
  object-fit: contain;
  border-radius: 10px;
  border: solid 1px #001845;;
}

img.keyboard{
  width: 15%;

  animation: rotate 10s linear infinite;
  align-self: center;
  justify-self: flex-start;
  

}

@keyframes rotate{
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

